<template>
    <div class="container d-flex align-items-center justify-content-center min-vh-100">
         <div class="column w-50">
             <div ref="listMessages" style="overflow: hidden; overflow-y: scroll; height: 300px;">
                 <dl class="row" v-for="message in this.$messages" :key="message.id">
                     <dt class="col-sm-3">{{ message.user }}:</dt>
                     <dd class="col-sm-9">{{ message.message }}</dd>
                 </dl>

                 <div ref="bottomEl"></div>
             </div>

                 <div v-if="showInput" class="form-group text-center">
                     <input ref="userName" v-model="userName" type="text" class="form-control" placeholder="Имя пользователя">
                     <button @click="connectToChat()" class="form-control btn btn-primary">Подключиться</button>
                 </div>
 
                 <div ref="formMessage" v-if="!showInput" class="form-group text-center">
                     <input ref="messageInput" type="text" class="form-control" placeholder="Сообщение">
                     <button @click="sendMessage()" class="form-control btn btn-primary">Отправить</button>
                 </div>
             
         </div>
         
         <div class="column w-30" style="margin-left: 10px;">
            <header>Подключенные пользователи</header>
            <div ref="listUsers" style="overflow-y: scroll; height: 360px;" class="border">
                 <dl class="row" v-for="user in this.$users" :key="user.id">
                     <dt class="col-sm-3">{{ user.userName }}</dt>
                 </dl>
             </div>
         </div>
     </div>
 </template>
 
 <script>
 export default {
   data(){
         return{
             userName: "",
             showInput : true,

             users: [],
             messages: [],
         }
     },

    sockets : {
        message (data){
             console.log(data.user + " " + data.message)

             this.$messages.push(data)
             this.$forceUpdate()
             setTimeout(() => {this.scrollToLast()}, 100)
        },

        connected (data) {
            console.log(data)
            this.$users = data.users
            this.$messages = data.messages
            this.$forceUpdate()
            this.scrollToLast()

        },
        
        newUser (data) {
            console.log("new user: " + data)
            this.$users.push(data)
            this.$forceUpdate()
        },

        removeUser (data) {
            this.$users.splice(data.id, 1)
            this.$forceUpdate()
        }
    },
 
mounted(){
    this.scrollToLast();
},

   methods : {
         connectToChat(){
             this.showInput = false

             this.$socket.emit('addUser', {userName: this.$refs.userName.value})

             this.userName =  this.$refs.userName.value

             window.addEventListener('beforeunload', (event) => {
                this.$socket.emit('removeClient', {userName: this.userName})
                event.returnValue = ''
             })
         },
     
         sendMessage(){
             var message = {
                 user:   this.userName,
                 message: this.$refs.messageInput.value
             }
 
             this.$socket.emit('message', message)
             this.$refs.messageInput.value = ""
         },

         scrollToLast(){
            console.log("scrolled")
            this.$refs.bottomEl?.scrollIntoView({ behavior: 'smooth' });
         },
     }
 }
 </script>
 
